@use 'global';

nav {
  padding: 20px;
  display: flex;
  max-height: 70px;
  flex-direction: row;
  align-items: center;

  .nav--logo {
    flex: 1;
  
    .logo{
      font-family: global.$logo-font; 
      font-size: 30px;
      text-transform: uppercase;
      color: global.$primary-color;
    }

    @media (max-width: 899px) {
      .logo {
        font-size: 2rem;
      }
    }

  }

  .nav--items {
    flex: 1;
    display: flex;
    justify-content: end;
  
    ul {
      display: flex;
  
      li {
        list-style: none;
        padding: 20px 20px;
  
        a {
          text-decoration: none;
          color: global.$primary-color;
          font-family: global.$primary-font;
          font-weight: 400;
          font-size: 18px;
          text-transform: uppercase;
        }
  
        a:hover {
          color: global.$hover-color;
        }

        @media (max-width: 899px) {
          a {
            font-size: 1rem;
          }
        }
      }

      @media (max-width: 899px) {
        li {
          padding: 8px;
        }
      }
    }

  }

 
}

